<template>
  <div class="IconPresenter">
    <div class="iconWrapper" v-for="path in icons" :key="path.name">
      <ActiveIcon
        :width="50"
        :height="50"
        :iconColor="'var(--grey-dark8)'"
        :path="path.path"
      />
      {{ path.name }}
    </div>
  </div>
</template>

<script setup lang="ts">
import ActiveIcon from "@/DesignSystem/Icons/IconSvg.vue";
import { reactive } from "vue";

const iconsListImport = import.meta.glob("/src/DesignSystem/Icons/List/*.ts");

interface Icon {
  name: string;
  path: string;
}

const icons: Icon[] = reactive([]);

for (const path of Object.keys(iconsListImport)) {
  iconsListImport[path]().then((mod: any) => {
    icons.push({
      name: path.split("/").pop()!.split(".").shift()!,
      path: mod.default,
    });
  });
}
</script>

<style scoped>
.IconPresenter {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 10px;
  grid-auto-rows: minmax(100px, auto);
}
.iconWrapper {
  border: solid 1px rgb(123, 123, 123);
  display: inline-block;
  box-shadow: 0px 0px 4px #7f7f7f;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 15px;
}
</style>